


.padWrapper {
    align-items: center;
    border-radius: 1rem;
    border: none;
    width: 100%;
    background-attachment: local;
    background-image: linear-gradient(to right, white 1rem, transparent 1rem),
        linear-gradient(to left, white 1rem, transparent 1rem),
        repeating-linear-gradient(white, white 1.9rem, #ccc 1.9rem, #ccc 2rem, white 2rem);

    line-height: 2rem;
    
    
}
.padInput {
    padding-left: 1rem;
    background: transparent;
    width: 96%;
    resize: none;
    border-radius: 1rem;
    border: none;
    outline: none;
    font-size: 2rem;
    color: black;

    :focus {
        outline: none;
    }
}

.hidden {
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s 5s, opacity 5s linear;
}
